@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 100;
  font-display: swap;
  src: url("../assets/fonts/Roboto-Thin.woff2") format("woff2"), url("../assets/fonts/Roboto-Thin.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-display: swap;
  src: url("../assets/fonts/Roboto-Light.woff2") format("woff2"), url("../assets/fonts/Roboto-Light.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: url("../assets/fonts/Roboto-Regular.woff2") format("woff2"), url("../assets/fonts/Roboto-Regular.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  font-display: swap;
  src: url("../assets/fonts/Roboto-Medium.woff2") format("woff2"), url("../assets/fonts/Roboto-Medium.woff") format("woff");
}
@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-display: swap;
  src: url("../assets/fonts/Roboto-Bold.woff2") format("woff2"), url("../assets/fonts/Roboto-Bold.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  min-width: 320px;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #FFFFFF;
  font-family: "Roboto", "Arial", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  color: #333B41;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  position: relative;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: 0;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.btn {
  border: 1px solid transparent;
  vertical-align: middle;
}

.overlay {
  position: fixed;
  background-color: rgba(41, 41, 41, 0.6);
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 999;
  display: flex;
}

.btn--orange {
  background: linear-gradient(113.96deg, #F9804B 1.49%, #FE9013 101.44%);
  padding: 15px 95px;
  font-family: Roboto;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 140%;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  outline: none;
  cursor: pointer;
}
.btn--orange:hover {
  background: #E47209;
}
.btn--orange:active {
  background: #4B9200;
}

.btn--submit {
  background: #F1F3F2;
  border: 1px solid #333B41;
  border-radius: 5px;
  color: #000000;
}

.button--close {
  position: fixed;
  background-color: transparent;
  right: 5%;
  top: 5%;
  width: 14px;
  height: 14px;
  display: block;
  border: 1px solid transparent;
  background-image: url("../assets/icons/x_icon.png");
  background-position: center;
  background-repeat: no-repeat;
  cursor: pointer;
  z-index: 1000;
}

.page-header {
  width: 100%;
  background-color: #000000;
}

.page-header__wrapper {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
}

.page-header__logo {
  width: 90px;
  padding: 0;
  margin: 14px 0;
  margin-bottom: 8px;
}

.page-header__logo img {
  width: 82px;
  height: 52px;
}

@media (max-width: 1599px) {
  .page-header__wrapper {
    max-width: 940px;
    margin: 0 auto;
  }
}
@media (max-width: 999px) {
  .page-header {
    position: fixed;
    z-index: 1000;
  }
  .page-header__wrapper {
    width: 94%;
    position: relative;
  }
  .page-header__logo {
    width: 47px;
    height: 31px;
    margin: 2px 0 1px 0;
  }
  .page-header__logo img {
    width: 47px;
    height: 31px;
    margin: 0;
  }
}
.main-nav__toggle {
  display: none;
}

.main-nav {
  width: 100%;
  position: relative;
  padding-left: 62px;
}

.main-nav__list {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  list-style: none;
  padding-left: 0;
  margin: 10px 0;
}

.main-nav__item {
  margin-right: 58px;
}

.main-nav__link {
  display: inline-block;
  color: #FFFFFF;
  font-weight: 500;
  cursor: pointer;
}
.main-nav__link:active {
  opacity: 0.7;
}

.main-nav__item--designed {
  margin-right: 0px;
  margin-left: auto;
}

.main-nav__link--active {
  color: #FE9013;
  cursor: default;
}

@media (max-width: 1599px) {
  .main-nav__item:not(.main-nav__item--designed) {
    margin-right: 48px;
  }
}
@media (max-width: 999px) {
  .main-nav {
    position: absolute;
    z-index: 1000;
    top: 0;
    right: 0;
    width: 100%;
    height: 330px;
    margin: 0px;
    background-color: #FFFFFF;
    padding-left: 0;
  }
  .main-nav--closed {
    position: fixed;
    right: -500px;
    transform: translateX(100%);
    transition: all 0.7s ease-out;
  }
  .main-nav--opened {
    position: fixed;
    right: 0;
    transform: translateX(0%);
    transition: all 0.7s ease-out;
  }
  .main-nav--opened::before {
    content: "";
    position: absolute;
    top: 10px;
    left: 10px;
    width: 66px;
    height: 42px;
    background-image: url("../assets/img/logo_open-menu_tablet.png");
    background-repeat: no-repeat;
    background-size: 66px 42px;
    z-index: 100;
  }
  .main-nav__toggle {
    position: absolute;
    background-color: transparent;
    width: 45px;
    height: 35px;
    top: 0px;
    right: -20px;
    z-index: 3000;
    display: block;
    cursor: pointer;
    padding: 0;
    outline: none;
    background-image: url("../assets/icons/burger_menu_tablet.png");
    background-repeat: no-repeat;
    background-position: 5px 10px;
    background-size: 18px 10px;
  }
  .main-nav__list {
    width: 320px;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 76px;
    margin-bottom: 213px;
  }
  .main-nav__item {
    text-align: left;
    padding-left: 10px;
    padding-top: 6px;
    padding-bottom: 9px;
  }
  .main-nav__link {
    display: block;
    color: #333B41;
    padding-bottom: 0;
  }
  .main-nav__link--active {
    color: #FE9013;
  }
  .main-nav__item--designed {
    margin-right: 0;
    margin-left: 0;
  }
}
@media (max-width: 639px) {
  .main-nav__toggle::before {
    left: 16px;
    top: 11px;
  }
}
