.pets {
  padding-top: 74px;
  padding-bottom: 115px;
  background-image: url("../assets/img/palm_bg_desktop.png"), url("../assets/img/strelitzia_desktop.png");
  background-repeat: no-repeat;
  background-position: -7px 722px, 1225px 907px;
  background-size: 292px 460px, 368px 275px;
  background-position: left bottom, right bottom;
}

.pets__wrapper {
  width: 100%;
  max-width: 1160px;
  margin: 0 auto;
  position: relative;
}

.pets__slider {
  position: relative;
  width: 100%;
  height: 892px;
  overflow: hidden;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  margin-bottom: 76px;
  padding-left: 0;
}

.pets__list {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  display: none;
  animation: 0.5s ease-in-out;
}

.pets__list--active,
.pets__list--next {
  display: block;
}

.pets__list--next {
  position: absolute;
  top: 0;
  width: 100%;
}

.to-left {
  animation-name: left;
}

.to-right {
  animation-name: right;
  animation-direction: reverse;
}

.from-left {
  animation-name: left;
  animation-direction: reverse;
}

.from-right {
  animation-name: right;
}

@keyframes left {
  from {
    left: 0;
  }
  to {
    left: -100%;
  }
}
@keyframes right {
  from {
    left: 100%;
  }
  to {
    left: 0;
  }
}
.pets__item {
  width: 366px;
  display: inline-block;
  position: relative;
  margin-bottom: 14px;
  margin-right: 25px;
  background-color: #FFFFFF;
  text-align: left;
  border: 1px solid #FE9013;
  border-radius: 5px;
  overflow: hidden;
}
